<template>
  <div class="page-list">

    <mt-header title="热门岗位">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>

    <nav class="tab-city" ref="tabCity">
      <mt-button @click.native="showPopup('province')" 
                size="large" 
                :class="{'active':popupStatus == 'province' && popupVisible}"
                >
        {{province}}
      </mt-button>
      <span class="tab-line"></span>
      <mt-button @click.native="showPopup('city')" 
                size="large"
                :class="{'active':popupStatus == 'city' && popupVisible}"
                >
        {{city}}
      </mt-button>

      <mt-popup v-model="popupVisible" popup-transition="popup-fade" class="city-popup" :style="{ top: buttonBottom + 'px' }">
        <ul>
          <li v-if="popupStatus == 'province'" 
              @click="selectProvince(item)"
              :class="{'active':item == province}"
              v-for="(item, index) in provinceList">
            {{item}}
          </li>
          <li v-if="popupStatus == 'city'"  
              @click="selectCity(item)"
              :class="{'active':item == city}"
              v-for="(item, index) in cityList">
            {{item}}
          </li>
        </ul>
      </mt-popup>
    </nav>

    <div class="list-infinite-wrapper" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
      <ul class="list-infinite-list" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="50">
        <job-item 
              v-for="(item, index) in list"
              key="index"
              href="part_time_post?id=123"
              title="兼职展会礼仪迎宾接待"
              salary="380" 
              location="杭州"
              time="5月25日起工作（共35天）" />
      </ul>
      <p v-show="loading" class="list-infinite-loading">
        <mt-spinner type="fading-circle"></mt-spinner>
        加载中...
      </p>
    </div>
    
  </div>
</template>

<script>
import jobItem from '../common/jobItem.vue'
import address from '../common/address.js'
import '../css/city.css';

export default {
  name: 'page-list',

  components: {
      jobItem
  },

  data() {
    return {
      provinceList:[],
      cityList:[],
      address: ['北京', '北京'],
      province: '北京',
      city: '北京',
      popupVisible: false,
      popupStatus: 'province',
      buttonBottom: 0,
      list: [],
      loading: false,
      allLoaded: false,
      wrapperHeight: 0
    };
  },

  methods: {
    showPopup (type) {
      this.popupStatus = type;  //切换省、城市
      
      this.popupVisible = true;
    },
    
    selectProvince (item) {
      this.province = item;
      this.cityList = address[item];
      this.city = address[item][0];   //默认选中第一个

      this.address = [this.province, this.city];

      this.popupVisible = false;
    },

    selectCity (item) {
      this.city = item;
      this.popupVisible = false;
    },

    loadMore() {
      this.loading = true;
      setTimeout(() => {

        let last = this.list[this.list.length - 1];
        for (let i = 1; i <= 10; i++) {
          this.list.push("http://www.cssoul.com/demo/weihai/company/12.jpg");
        }
        this.loading = false;
      }, 1000);
    }
  },

  created() {
    for (let i = 1; i <= 10; i++) {
      this.list.push(i);
    }

    this.provinceList = Object.keys(address);
    this.cityList = address[this.province];
  },

  mounted() {
    setTimeout(() => {
      this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;

      this.buttonBottom = this.$refs.tabCity.getBoundingClientRect().bottom;
    }, 100);
  }
};
</script>


