<template>
  <div class="page-post">

    <mt-header title="岗位详情">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>

    <div class="post-info">
      <mix-box 
        image="http://www.cssoul.com/demo/weihai/company/6.jpg" 
        title="大堂经理" 
        salary="【3500-5000】" 
        source="麦当劳 McDonald's">
      </mix-box>
      <div class="post-info-foot">
        <span>包吃住</span>
        <span>缴纳社保</span>
        <span>福利优厚</span>
      </div>
    </div>
    
    <div class="box box-loc">
      <common-title iconClass="icon-loc" text="浙江省杭州市余杭区梦想小镇37栋1楼"> </common-title>
    </div>

    <div class="box">
      <common-title iconClass="icon-money" text="我能挣多少钱"> </common-title>
      <div class="box-desc">
        我能挣多少钱的内容我能挣多少钱的内容我能挣多少钱的内容我能挣多少钱的内容我能挣多少钱的内容我能挣多少钱的内容我能挣多少钱的内容
      </div>
    </div>

    <div class="box">
      <common-title iconClass="icon-info" text="我需要做什么"> </common-title>
      <div class="box-desc">
        <p>
          1、餐厅服务员应在不同的工作站岗位上服务顾客，协助餐厅达到最佳的质量、服务、清洁及物超所值

        </p>
        <p>
          2、从实地训练、区域管理及铺面管理，学习基本的餐厅营运知识
        </p>
        <p>
          3、管理指定值班的员工、产品及设备，以达到最佳的质量、服务、清洁及物超所值
        </p>
      </div>
    </div>

    <div class="box">
      <common-title iconClass="icon-welfare-blue" text="我能享受的福利待遇"> </common-title>
      <div class="box-desc">
        <p>1、底薪：3500到4000。额外加400的话费补贴</p>
        <p>2、10%到55%的提成，百分之70的人月收入过万</p>
        <p>3、培训：麦当劳企业特有的带薪培训与后期进阶培训体系；（包括新人班、进阶班、精英班、储备干部班等）</p>
      </div>
    </div>

    <div class="box">
      <common-title iconClass="icon-condition" text="录用条件"> </common-title>
      <div class="box-desc">
        <p>1、大专及以上学历</p>
        <p>2、年龄20周岁及以上</p>
        <p>3、上进心强，能吃苦，抗压力强</p>
      </div>
    </div>

    <div class="box">
      <common-title iconClass="icon-attention" text="报名须知"> </common-title>
      <div class="box-desc">
        这里是申请职位的内容这里是申请职位的内容这里是申请职位的内容。
      </div>
    </div>

    <div class="fix-bottom fix-post">
      <mt-button @click.native="handleAsk">
        <img src="../../images/consultation_online_icon@3x.png" height="20" width="20" slot="icon">
        在线咨询
      </mt-button>
      <mt-button @click.native="handleTell">
        <img src="../../images/consultation_call_icon@3x.png" height="20" width="20" slot="icon">
        电话咨询
      </mt-button>
      <mt-button @click.native="handleApply" type="primary">
        我要应聘
      </mt-button>
    </div>  

</div>
</template>

<script>
import commonTitle from '../common/title.vue'
import mixBox from '../common/mixBox.vue'

export default {
  name: 'page-post',

  components: {
      commonTitle,
      mixBox
  },
  methods: {
    handleAsk: function() {
      this.$messagebox({
        title: '提交成功',
        message: '微海已经收到您的信息啦～请耐心等待回复哦！',
        type:'alert',
        showCancelButton: false,
        confirmButtonText: '好的'
      }).then(action => {
        console.log(action);
      });
    },
    handleTell: function() {
      location.href = 'tel: 18329169978';
    },
    handleApply: function() {

      this.$messagebox({
        title: '未注册',
        message: '您还没有注册，注册一下就可以尽情使用里面的功能喽～',
        type:'confirm',
        showCancelButton: true,
        confirmButtonText: '去注册',
        cancelButtonText: '等一下',
      }).then(action => {
        if(action == "confirm"){
          this.$router.push({ path: 'user/login', query: { refer: 'post' }})
        }else{
          console.log(action);
        }
      });

    }
  }
};
</script>

<style lang="css">
  .page-post{
    background:#f0f0f0;
    padding-bottom:50px;
  }

  .post-info{
    padding:20px 15px;
    background:#fff;
  }
  .post-info-foot{
    margin-top:15px;
  }
  .post-info-foot span{
    display: inline-block;
    margin-right:12px;
    font-size: 14px;
    padding: 0 12px;
    height: 33px;
    line-height:33px;
    border: 1px solid #D7D7D7;
    border-radius:4px;
    background:#fff;
    color: #828282;
  }

  .box-loc{
    border-bottom:none;
  }

  .fix-post{
    display:flex;
  }
  .fix-post .mint-button--normal{
    flex: 1;
    background:#fff;
    font-size:14px;
    
    box-shadow:none;
    font-size:13px;
  }
  .fix-post .mint-button:first-child{
    border-right:1px solid #E9E9E9;
  }
  .fix-post .mint-button--normal label{
    display:block;
  }
  .fix-post .mint-button--primary{
    flex: 2;
    background: #0977C5;
    box-shadow:none;
    font-size:16px;
  }
</style>



